    border-color: #dfd3c3;
    background-color: #475860;
}
.title-section-hobby {
  color: #1b262c;
}
.carousel-indicators-hobby li {
  background-color: #1b262c;
}
.carousel-indicators-hobby .active {
  background-color: #1b262c;
}
.carousel-control-prev-icon-hobby {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%231b262c' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E");
}
.carousel-control-next-icon-hobby {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%231b262c' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E");
}

@media(max-width:768px){
  #catalogueCarousel {
    width: 100% !important;
  }
}



/*References*/
.reference-img{
  height: 5rem;
  width: 5rem;
  margin: 0.4rem 1rem;
  border-radius: 100%;
}
@media (max-width: 850px) {
    .reference-img {
        height: 2.5rem;
        width: 2.5rem;
        margin: 0.2rem 0.5rem;
    }
}
.reference-reference{
  font-size: 1rem;
  padding-top: 0.4rem;
  padding-bottom: 5rem;
  white-space:pre-line;
  font-style: italic;
  vertical-align: middle;
  text-align: left;
  align-items: middle;
  align-self: middle;
  align-content: middle;
}
@media (max-width: 850px) {
    .reference-ref {
        font-size: 0.7rem;
    }
}
@media (max-width: 576px) {
    .reference-ref {
        font-size: 0.5rem;
    }
}
.reference-person{
  font-size: 1.2rem;
  font-weight: bold;
  vertical-align: middle;
  text-align: left;
  align-items: middle;
  align-self: middle;
  align-content: middle;
}
@media (max-width: 850px) {
    .reference-person {
        font-size: 0.7rem;
    }
}
@media (max-width: 576px) {
    .reference-person {
        font-size: 0.5rem;
    }
}

.reference-date{
  font-size: 0.8rem;
  vertical-align: middle;
  text-align: left;
  align-items: middle;
  align-self: middle;
  align-content: middle;

}
@media (max-width: 850px) {
    .reference-date {
        font-size: 0.7rem;
    }
}
@media (max-width: 576px) {
    .reference-date {
        font-size: 0.5rem;
    }
}
.reference-container {
  display: flex;
  justify-content: center;
  align-items: center;
}




/*Footer*/
footer {
  background-color: #1b262c;
  color:#dfd3c3;
  text-align: center;
  bottom: 0;
  padding: 3% 0;
}
@media (max-width: 850px) {
    footer {
        padding: 6% 0;
    }
}
footer p {
  color:#dfd3c3;
  margin: 0;
}
.footer-symbol {
  color:#dfd3c3;
  font-size: 1.5rem;
}
.link-decoration {
  text-decoration: none;
}
.footer-symbol:hover {
  opacity: 0.6;
}
